.CardRecent {
  animation: slideDown 1s ease forwards;
  display: flex;
  background-color: #fff;
  border-radius: 18px;
  width: 280px;
  height: 150px;
  padding: 20px;
  box-shadow: 2px 4px 12px rgba(0, 0, 0, 0.08);
  transition: all 0.3s cubic-bezier(0, 0, 0.5, 1);
  flex-wrap: wrap;
  flex-direction: column;
}
.items-title {
  display: flex;
  align-items: center;
  gap: 20px;
}
.title {
  font-size: 20px;
  color: #0175fb;
}
.disc {
  display: flex;
  flex-wrap: wrap;
}
